"use client";

import { useTimer } from "@/hooks/useTimer";
import { Play, Pause, RotateCcw } from "lucide-react";
import clsx from "clsx";

export default function FocusPage() {
  const {
    timeLeft,
    isActive,
    mode,
    startTimer,
    pauseTimer,
    resetTimer,
    setTimerMode,
    formatTime,
  } = useTimer();

  return (
    <div className="flex flex-col items-center justify-center">
      <h1 className="mb-8 text-3xl font-bold text-gray-800">Focus Timer</h1>

      <div className="mb-8 flex rounded-lg bg-white p-1 shadow-sm">
        <button
          onClick={() => setTimerMode("focus")}
          className={clsx(
            "rounded-md px-6 py-2 font-medium transition-colors",
            mode === "focus"
              ? "bg-blue-100 text-blue-600"
              : "text-gray-600 hover:bg-gray-50"
          )}
        >
          Focus
        </button>
        <button
          onClick={() => setTimerMode("shortBreak")}
          className={clsx(
            "rounded-md px-6 py-2 font-medium transition-colors",
            mode === "shortBreak"
              ? "bg-green-100 text-green-600"
              : "text-gray-600 hover:bg-gray-50"
          )}
        >
          Short Break
        </button>
        <button
          onClick={() => setTimerMode("longBreak")}
          className={clsx(
            "rounded-md px-6 py-2 font-medium transition-colors",
            mode === "longBreak"
              ? "bg-purple-100 text-purple-600"
              : "text-gray-600 hover:bg-gray-50"
          )}
        >
          Long Break
        </button>
      </div>

      <div className="mb-12 text-9xl font-bold text-gray-800 tabular-nums">
        {formatTime(timeLeft)}
      </div>

      <div className="flex gap-6">
        <button
          onClick={isActive ? pauseTimer : startTimer}
          className={clsx(
            "flex h-16 w-16 items-center justify-center rounded-full shadow-lg transition-transform hover:scale-105 active:scale-95",
            isActive
              ? "bg-yellow-500 text-white hover:bg-yellow-600"
              : "bg-blue-600 text-white hover:bg-blue-700"
          )}
        >
          {isActive ? (
            <Pause className="h-8 w-8 fill-current" />
          ) : (
            <Play className="ml-1 h-8 w-8 fill-current" />
          )}
        </button>
        <button
          onClick={resetTimer}
          className="flex h-16 w-16 items-center justify-center rounded-full bg-gray-200 text-gray-600 shadow-lg transition-transform hover:bg-gray-300 hover:scale-105 active:scale-95"
        >
          <RotateCcw className="h-8 w-8" />
        </button>
      </div>
    </div>
  );
}
